import React from "react";
import HistoryItem from "./HistoryItem";
import HistoryImg from "./History.svg";
import "./History.css";

const History = () => {
  const items = [
    {
      id: 1,
      name: "Item1",
      price: 5000,
      amount: 1
    },
    {
      id: 2,
      name: "Galaxy S10 5G (뭔개소리)",
      price: 10000,
      amount: 1
    }
  ];

  const historyItems = items.map(item => (
    <HistoryItem key={item.id} item={item} />
  ));

  const purchaseDate = "2019-01-02 21:32";

  const totalPrice = items.reduce(
    (sum, item) => sum + Number(item.price) * Number(item.amount),
    0
  );

  const watermark = {
    backgroundImage: `url(${HistoryImg})`,
    backgroundRepeat: "no-repeat",
    backgroundAttachment: "fixed",
    backgroundSize: "250px",
    backgroundPosition: "center"
  };

  return (
    <div className="History">
      <div className="History-watermark" style={watermark}></div>
      <div className="History-items">{historyItems}</div>
      <div className="History-info">
        <div className="History-date">{purchaseDate}</div>
        <div className="History-price">
          ₩ {Number(totalPrice).toLocaleString()}
        </div>
      </div>
    </div>
  );
};

export default History;
